.app {
  max-width: 768px;
  margin: 0 auto;
  padding: 16px 16px;
}

.nav {
  padding: 16px 16px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--divider-color);
  background-color: var(--bg-color);
  position: sticky;
  top: 0;
  z-index: 1;
}

.nav > *:not(:last-child) {
  margin-right: 8px;
}

.nav .picture {
  line-height: initial;
}

.title {
  color: var(--font-color);
  text-decoration: none;
}

.title > * {
  margin: 0;
}

.tools {
  flex-grow: 1;
}

.action-row {
  display: flex;
  margin-top: 16px;
  margin-bottom: 48px;
  justify-content: center;
}

.logo {
  width: 32px;
}

.icon-link {
  color: var(--font-color);
}

.control-button {
  margin: 0 auto;
  padding: 24px 48px;
  border: 0;
  border-radius: 4px;
  font-size: 1.3em;
  font-weight: 700;
  box-shadow: 0.75em 0.75em #000;
  background-color: #34495e;
  color: #fff;
}

.control-button:hover {
  box-shadow: 0.5em 0.5em #000;
  transform: translate(0.25em, 0.25em);
  outline: none;
}

.control-button:active {
  transform: translate(0.5em, 0.5em);
  box-shadow: 0.25em 0.25em #000;
  outline: none;
}
.control-button:visited,
.control-button:focus {
  outline: none;
}

@media screen and (max-width: 576px) {
  .control-button {
    padding: 24px 48px;
    font-size: 1em;
    font-weight: 700;
  }
  .control-button:hover {
    box-shadow: 0.75em 0.75em #000;
    transform: translate(0, 0);
    outline: none;
  }
  .control-button:active {
    transform: translate(0.5em, 0.5em);
    box-shadow: 0.25em 0.25em #000;
    outline: none;
  }
}
